   .white_key { 
        position: absolute; 
        background-color: #fff;
        transition: background-color 0.1s ease;
        border:2px solid #000;
        border-radius:5px; 
        width: calc(1.4vw) ;
        height: calc(15vw);
        cursor:pointer;
        margin:0px 0px;
        float:left;
      }

.white_key:hover {
        background-color:#C0C0C0;
      }

      .black_key {
        position: absolute; 
        background-color: #000;
        border-radius:3px; 
        width: calc(1vw);
        height: calc(9vw);
        cursor:pointer;
        margin:0px 0px;
        float:left;
      }

.black_key:hover {
        background-color:#303030;
      }
